.option__wrapper {
  position: relative;

  padding: 18px;
  margin-bottom: 30px;

  background-color: $basic-grey;

  @media (min-width: $tablet-width) {
    padding-top: 77px;
    padding-bottom: 53px;
    padding-left: 52px;
    padding-right: 62px;
  }
}

.option__slim {
  position: absolute;
  top: 21px;
  left: 22px;

  width: 50px;
  height: 53px;

  @media (min-width: $tablet-width) {
    top: 22%;
    right: 9%;
    left: auto;

    width: 200px;
    height: 211px;
  }

  @media (min-width: $desktop-width) {
    top: 44px;
    left: 8%;
    right: auto;

    width: 100px;
    height: 106px;
  }
}

.option__pro {
  position: absolute;
  top: 24px;
  left: 13px;

  width: 67px;
  height: 50px;

  @media (min-width: $tablet-width) {
    top: 22%;
    right: 4%;
    left: auto;

    width: 268px;
    height: 200px;
  }

  @media (min-width: $desktop-width) {
    top: 44px;
    left: 5%;
    right: auto;

    width: 134px;
    height: 100px;
  }
}

.option__title {
  margin-bottom: 29px;
  margin-left: 73px;

  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  text-align: left;

  @media (min-width: $tablet-width) {
    margin-bottom: 24px;
    margin-left: 0;

    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 73px;

    text-align: center;
  }
}

.option__text {
  display: block;

  text-align: left;

  @media (min-width: $tablet-width) {
    width: 60%;
    margin-bottom: 48px;

    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    width: 90%;
    margin-bottom: 23px;
  }
}

.option__text::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: 23px;
  margin-bottom: 15px;

  background-color: $special-grey-6;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.option__link {
  position: relative;

  display: block;

  font-size: 16px;
  line-height: 16px;
  text-align: left;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.option__arrow {
  position: absolute;
  top: 2px;
  left: 105px;

  width: 24px;
  height: 12px;

  @media (min-width: $tablet-width) {
    top: 8px;
    left: 132px;
  }
}
